.experience {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  margin-top: 2rem;
  .experience-left {
    display: flex;
    flex-direction: column;
    max-width: 391px;
    .tags {
      display: flex;
      flex-direction: row;
      gap: 1rem;
      margin-bottom: 0.5rem;
      flex-wrap: wrap;
    }
    .experience-title {
      font-size: 42px;
      margin: 0;
    }
    .experience-desc {
      margin: 0;
      font-weight: 500;
      margin-top: 0.5rem;
      margin-bottom: 1rem;
    }
  }
  .experience-right {
    img {
      width: 517px;
      height: 285px;
      border-radius: 10px;
    }
  }

  @media screen and (max-width: 1024px) {
    flex-direction: column;
    gap: 2rem;
    .experience-left {
      order: 1;
      .tags {
        justify-content: center;
      }
    }
    .experience-left,
    .experience-right {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .experience-desc {
        text-align: center;
      }
    }
  }

  @media screen and (max-width: 700px), (max-height: 700px) {
    video,
    img {
      aspect-ratio: 1.8140350877;
      width: 100% !important;
      height: auto !important;
    }
  }
}
